.footer {
    background-color: #c9c6cc; /* Color de fondo del pie de página */
    color: white; /* Color del texto */
    padding: 10px 20px; /* Espaciado interior del pie de página */
    display: flex;
    flex-direction: column; /* Coloca los elementos en columna */
    align-items: center; /* Alinea los elementos horizontalmente */
    box-shadow: inset -5px 0px 100px rgba(0, 0, 0, 0.7); 
}

.footer-content {
    text-align: left; /* Alinea el texto a la izquierda */
    margin-bottom: 0px; /* Espacio entre el contenido y el año */
}

.footer-content p {
    margin: 6; /* Elimina el margen predeterminado de los párrafos */
}

.footer-year {
    text-align: center; /* Centra el año */
}



